$textBoxPadding: 10px 20px;
$maxWidth: 500px;
$commonBorder: solid 2px #000;

body {
  font-size: 20px;
  margin: 0;
  font-family: monospace;

  & input {
    font-size: 20px;
  }
}

.type-training-container {
  font-family: monospace;
  text-align: left;
  max-width: $maxWidth;

  .input-area {
    border: solid 2px #000;
    border-width: 0 2px
  }

  .input-box {
    box-sizing: border-box;
    padding: $textBoxPadding;
    padding-top: 0;
    padding-bottom: 0;
    font-family: monospace;
    background-color: #00000006;
    width: 100%;
    height: 2.4em;
    line-height: 2.4em;

    input {
      margin: 0 auto;
      width: 100%;
      outline-style: none;
      padding: 0;
      border: none;
      background: transparent;
      &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: #0002;
      }
    }
  }

  .target-string {
    background-color: #00000006;
    padding: $textBoxPadding;
    max-height: 8em;
    overflow: hidden;
    .charactar-list {
      display: flex;
      position: relative;
      flex-wrap: wrap;

      .match {
        color: #00f5;
      }
      .notmatch {
        color: #f006;
        background: #f001;
      }
      .default {
        color: #0005;
      }
      .passed {
        color: #000;
      }
    }
    .one-line {
      flex-wrap: nowrap;
    }
  }

  .analyze-info {
    padding: $textBoxPadding;
    background-color: #000;
    color: #fff;
    font-size: 0.5em;
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;
    & > div {
      margin-right: 1.4em ;
    }
  }

  .progress-bar {
    height: 4px;
    display: flex;
    background: #0001;
    
    .progress {
      background: #0006;
    }
  }
}

.charactar-place-holder {
  display: inline-block;
  background-color: #0003;
  height: 1em;
}

.charactar-cube {
  // display: inline-block;
  // height: 1.4em;
  // line-height: 1.4em;
  // width: 1.4em;
  // border: 1px solid #0003;
  text-align: center;
  user-select:none;
}

#dev-info {
  display: none;
  color: #aaa;
  font-size: 0.4em;
  color: #f005;
  position: fixed;
  top: 100px;
  // left: 410px;
  margin: 0 0.5em;
}

.target-setting {
  font-family: monospace;
  max-width: $maxWidth;
  font-size: 20px;

  textarea {
    font-size: 20px;
    margin: 1em 0;
    height: 10em;
    width: 90%;
    resize: none;
    outline-style: none;
    // padding: $textBoxPadding;
    border: none;
    color: #000;
    background: transparent;
    &::placeholder {
      color: #0002;
    }
  }

  button {
    padding: $textBoxPadding;
    font-size: 0.5em;
    color: #fff;
    background-color: #000;
    border-color: #000;
    border-width: 2px;
    // border-radius: 5px;
    width: 100%;

    &:hover {
      background-color: #555;
    }
  }
}

.statistics {
  padding: $textBoxPadding;
  font-size: 0.5em;
  max-width: $maxWidth;
  font-family: monospace;
  text-align: left;

  .slowZi {
    display: flex;
    flex-wrap: wrap;
  }
}

.main-box {
  border: $commonBorder;
  border-Top: none;
}
